body {
  color: #fff;
  background: linear-gradient(#0f2027, #203a43, #2c5364);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.glass-card {
  text-align: center;
  backdrop-filter: blur(15px);
  background: #ffffff1a;
  border-radius: 20px;
  width: 90%;
  max-width: 500px;
  padding: 2rem;
  box-shadow: 0 0 20px #0006;
}

.glass-card h1 {
  font-size: 2rem;
  font-weight: bold;
}

.glass-card p {
  opacity: .8;
  margin-bottom: 1.5rem;
  font-size: 1rem;
}

.tab-buttons {
  justify-content: center;
  margin-bottom: 1rem;
  display: flex;
}

.tab-buttons button {
  color: #fff;
  cursor: pointer;
  background: #1e2a38;
  border: none;
  border-radius: 10px;
  margin: 0 .3rem;
  padding: .5rem 1rem;
}

input {
  box-sizing: border-box;
  border: none;
  border-radius: 12px;
  width: 100%;
  margin-top: 1rem;
  padding: 1rem;
}

button.download {
  color: #fff;
  cursor: pointer;
  background: #1a73e8;
  border: none;
  border-radius: 10px;
  align-items: center;
  gap: .5rem;
  margin-top: 1rem;
  padding: .8rem 1.2rem;
  font-weight: bold;
  display: inline-flex;
}
